import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { TextField } from '@material-ui/core';
import mPasien from '../../../../Controler/c_Pasien'
import validate from 'validate.js'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';


const ModalPasien = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailUser || {}
    const klinik = props.klinik
    const [isPassword, setIsPassword] = useState(true)

    const [form, setForm] = useState({
        username: '',
        nama: '',
        noHp: "",
        password: "",
        alamat: ""
    })
    useEffect(() => {
        if (detail.username) {
            setIsPassword(false)
            setForm({
                username: detail.username,
                nama: detail.nama,
                noHp: detail.noHp,
                alamat: detail.alamat
            })
        }
        return () => {

        }
    }, [detail])
    const handleClose = () => {
        props.setOpen(false)
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        setForm(newForm);
    }
    const validateForm = () => {
        const { username, nama, noHp, alamat } = form;
        const promForm = new Promise((resolve, reject) => {
            const constraint = {
                username: {
                    email: {
                        message: "Masukan email yang valid"
                    }
                },
                nama: {
                    length: {
                        minimum: 3,
                        message: "Nama lengkap minimal terdiri dari 3 huruf"
                    },
                },
                noHp: {
                    length: {
                        minimum: 7,
                        message: "Nomor HP minimal terdiri dari 7 digit"
                    },
                },
                alamat: {
                    length: {
                        minimum: 5,
                        message: "Alamat minimal terdiri dari 5 Huruf"
                    },
                }
            }
            const validator = validate({ username, nama, noHp, alamat }, constraint, { fullMessages: false });
            if (validator === undefined) {
                resolve();
            } else {
                reject(validator);
            }
        })
        return Promise.all([promForm]);
    }
    const handleSimpan = () => {
        validateForm().then(() => {
            const { username, nama, noHp, password, alamat } = form;
            if (isPassword && (password.length < 8)) return Notifikasi("warning", "Password minimal 8 digit")
            mPasien.simpanPasien(clientID, token, klinik.kodeklinik, klinik.namaklinik, username, nama, noHp, password, alamat).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    props.setIsReload(true)
                    handleClose()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }).catch(err => {
            const keys = Object.keys(err);
            for (const key of keys) {
                Notifikasi("warning", err[key])
            }
        })
    }
    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>
                {detail.username ? ("Update") : ("Add")} Dokter
                    </ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12}>
                        <TextField
                            id="username"
                            name="username"
                            label="E-mail"
                            fullWidth
                            value={form.username}
                            onChange={handleChange}
                            variant="outlined"
                            type="mail"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        {detail.username && (
                            <FormControlLabel control={<Checkbox checked={isPassword} onChange={(e) => setIsPassword(e.target.checked)} name="isPassword" color="primary" />} label="Ubah Password" />
                        )}
                        {isPassword && (
                            <TextField
                                id="password"
                                name="password"
                                label="Password"
                                fullWidth
                                value={form.password}
                                onChange={handleChange}
                                variant="outlined"
                                type="password"
                            />
                        )}
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="nama"
                            name="nama"
                            label="Nama Lengkap"
                            fullWidth
                            value={form.nama}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="noHp"
                            name="noHp"
                            label="HP"
                            fullWidth
                            value={form.noHp}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="alamat"
                            name="alamat"
                            label="Alamat"
                            fullWidth
                            value={form.alamat}
                            onChange={handleChange}
                            variant="outlined"
                            multiline
                            rows="3"
                        />
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSimpan}>Save Pasien</Button>{" "}
            </ModalFooter>
        </Modal>
    )
}

export default ModalPasien;