import GQL from '../helpers/GQL'


const User = {
	listPasien: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listUser(jenisUser:"pasien", kodeklinik:"${kodeklinik}"){
                        username
                        nama
                        noHp
						alamat
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listUser) return (resolve(resp.data.listUser));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    simpanPasien: (clientID, token,  kodeklinik, namaklinik,  username, nama, noHp, password, alamat) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    simpanUser(input:{username:"${username}", password:"${password}", nama:"${nama}", noHp:"${noHp}", jenisUser:"pasien", validasi: true, alamat:"${alamat}"},kodeklinik: "${kodeklinik}", namaklinik: "${namaklinik}")
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default User;
